.header {
    $this: &;
    background: $blue-dark;
    padding: 13px 0;
    .container {
        max-width: 1350px;
    }
    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__logo {
    }

    &__nav {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 40px;
    }

    &__items {
    }

    &__link {
        display: block;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 17px;
        letter-spacing: 0em;
        text-align: left;
        color: $white;
        position: relative;
        &::after{
            display: block;
            content: '';
            position: absolute;
            height: 1px;
            width: 0%;
            bottom: -5px;
            left: 0;
            background: $white;
            opacity: 0;
            transition: width 0.2s ease,opacity 0.2s ease;
        }
        &:hover{
            &::after{
                opacity: 1;
                width: 100%;
            }
        }
        &--active {
            font-weight: 700;
        }
        &--yellow {
            display: flex;
            align-items: center;
            gap: 9px;
            color: $secondary;
            &::after{
                background: $secondary;
            }
        }
    }

    &__link-text {
    }

    &__link-img {
    }

    &__contact {
        display: flex;
        align-items: center;
        gap: 12px;
    }

    &__left,
    &__right {
        display: flex;
        gap: 10px;
    }
    &__left {
    }

    &__left-img,
    &__right-img {
        height: 17px;
        width: 17px;
    }
    &__left-text {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: 17px;
        color: $white;
    }

    &__right {
        height: 44px;
        width: 214px;
        justify-content: center;
        align-items: center;
        border: 1px solid $white;
        border-radius: 2px;
    }
    &__right-text {
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 17px;
        color: $white;
    }
}
