.form {
    margin-top: 70px;
    margin-bottom: 100px;
    &__inner {
        display: flex;
        justify-content: center;
    }

    &__left {
        max-width: 424px;
        position: relative;
    }

    &__title {
        font-family: Montserrat;
        font-size: 24px;
        font-style: normal;
        font-weight: 800;
        line-height: 34px;
        color: $primary;
    }

    &__img {
        position: absolute;
        left: -90px;
        top: -56px;
    }

    &__right {
        max-width: 360px;
        display: flex;
        align-items: flex-end;
        flex-direction: column;
        gap: 20px;
        width: 100%;
    }

    &__input {
        max-height: 100px;
        overflow-y: hidden;
        min-height: 20px;
        width: 100%;
        border: none;
        background: transparent;
        font-family: Montserrat;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 17px;
        color: $primary;
        position: relative;
        padding-bottom: 6px;
        border-bottom: 2px solid #EFEFEF;
    }

    &__btn {
        width: 214px;
        margin-top: 10px;
    }
}
